export default {
	data() {
		return {
			swiperList: [],
			newsList: [],
			httpNum: 0
		}
	},
	onLoad() {
		let _this = this;
		uni.showLoading({
			title: '加载中',
			mask: true
		});
		_this.request({
			url: _this.http.getBanner,
			data: {
				pageNum: '1',
				pageSize: '4'
			},
			success: (res) => {
				if (res.code === '0') {
					_this.swiperList = res.result;
				}
				_this.httpNum++;
				_this.countHttp();
			}
		}, false);
		_this.request({
			url: _this.http.getNewsList,
			data: {
				pageNum: '1',
				pageSize: '3',
				newsType: '1'
			},
			success: (res) => {
				if (res.code === '0') {
					_this.newsList = res.result;
				}
				_this.httpNum++;
				_this.countHttp();
			}
		}, false);
	},
	methods: {
		countHttp() {
			if (this.httpNum === 2) {
				uni.hideLoading();
			}
		},
		// 轮播图点击跳转到领取优惠券页面
		receiveCoupon() {
			uni.navigateTo({
				url: '/pages/receiveCoupon/receiveCoupon'
			});
		}
	}
}
